<template>
    <div class="out-box">
      <common-header :tittle="tittle" :showleft="true"  :routerLeft="routerLeft" ></common-header>
      <div class="out-one">
        <!-- photo -->
        <li @click="AccountSetting(photo)"><span>账号设置</span><img :src="imgFileUrl+photo" alt=""><van-icon name="arrow" /></li>
        <li @click="invite "><span>邀请好友</span><van-icon name="arrow" /></li>
      </div>
      <div class="out-one">
        <li @click="feedback"><span>帮助与反馈</span><van-icon name="arrow" /></li>
        <li @click="about"><span>关于我们</span><van-icon name="arrow" /></li>
        <li @click="contact"><span>联系客服</span><van-icon name="arrow" /></li>
      </div>
      <div class="out-one"><!--checkversion(1)-->
        <li @click="UpPackage"><span>检查版本与更新</span><van-icon name="arrow" /></li>
        <li><span>清除缓存</span><span>2.2M</span><van-icon name="arrow" /></li>
      </div>
      <div v-show="this.isshow" class="newversion">
        <img src="../../assets/image/newversion.png" alt="">
        <van-icon name="close" class="close" @click="close"/>
        <van-button type="primary" class="checkbtn">更新</van-button>
      </div>
      <van-dialog v-model="showFriend" show-cancel-button :before-close="beforeClose" confirm-button-text="发送" >
        <van-field v-model="username" label="姓名" placeholder="请输入姓名"  class="dialog-inp" />
        <van-field v-model="tel" type="phone" label="手机号" placeholder="请输入手机号" class="dialog-inp" />
      </van-dialog>
       <van-dialog v-model="phoneisShow" show-cancel-button :before-close="beforeClose" confirm-button-text="确定" >
        <van-field v-model="phone" label="手机号"  class="dialog-inp" />
      </van-dialog>
      <!--<van-button  @click="dropout">退出当前账号</van-button>-->
    </div>
</template>

<script>
import commonHeader from 'common/common-header'
import { Dialog, Toast } from 'vant'
import { imgFileUrl } from '../../apiconfig/index'
export default {
  name: 'out',
  components: {commonHeader},
  data() {
    return {
      tittle: '设置',
      routerLeft: '/day',
      username: '',
      tel: '',
      showFriend: false,
      version: false,
      isshow: false,
      photo: this.$route.params.urll,
      imgFileUrl: imgFileUrl,
      phoneisShow: false,
      phone: localStorage.getItem('partnerPhone')
    }
  },
  mounted() {
    console.log(this.$route.params.urll)
  },
  methods: {
    UpPackage() {
      // 获取当前应用的版本号
      var wgtVer = null
      function plusReady () {
        // 获取本地应用资源版本号
        plus.runtime.getProperty(plus.runtime.appid, function (inf) {
          wgtVer = inf.version
          alert('当前应用版本：' + wgtVer)
        })
      }
      if (window.plus) {
        plusReady()
      } else {
        document.addEventListener('plusready', plusReady, false)
      };
      // 发起ajax检测是否有新版本
      var checkUrl = ''
      function checkUpdate () {
        alert('检测更新')
        plus.nativeUI.showWaiting('检测更新...')
        var xhr = new XMLHttpRequest()
        xhr.onreadystatechange = function () {
          switch (xhr.readyState) {
            case 4:
              plus.nativeUI.closeWaiting()
              if (xhr.status === 200) {
                alert('检测更新成功：' + xhr.responseText)
                var newVer = xhr.responseText
                if (wgtVer && newVer && (wgtVer !== newVer)) {
                  downWgt()  // 下载升级包
                } else {
                  plus.nativeUI.alert('无新版本可更新！')
                }
              } else {
                alert('检测更新失败！')
                plus.nativeUI.alert('检测更新失败！')
              }
              break
            default:
              break
          }
        }
        xhr.open('GET', checkUrl)
        xhr.send()
      }
      checkUpdate()
      // 下载wgt文件
      var wgtUrl = 'http://服务器域名/文件夹/mananger_tool/update/update.wgt'
      function downWgt () {
        plus.nativeUI.showWaiting('下载wgt文件...')
        plus.downloader.createDownload(wgtUrl, {filename: '_doc/update/'}, function (d, status) {
          if (status === 200) {
            alert('下载wgt成功：' + d.filename)
            installWgt(d.filename) // 安装wgt包
          } else {
            alert('下载wgt失败！')
            plus.nativeUI.alert('下载wgt失败！')
          }
          plus.nativeUI.closeWaiting()
        }).start()
      };
      // 更新应用资源
      function installWgt (path) {
        plus.nativeUI.showWaiting('安装wgt文件...')
        plus.runtime.install(path, {}, function () {
          plus.nativeUI.closeWaiting()
          alert('安装wgt文件成功！')
          plus.nativeUI.alert('应用资源更新完成！', function () {
            plus.runtime.restart()
          })
        }, function (e) {
          plus.nativeUI.closeWaiting()
          console.log("安装wgt文件失败["+e.code+"]："+e.message)
          plus.nativeUI.alert("安装wgt文件失败["+e.code+"]："+e.message)
        })
      }
    },
    close() {
      this.isshow = false
    },
    checkversion(id) {
      if (id === 1) {
        Dialog.confirm({
          title: '已是最新版本',
          message: '1.1.1.1'
        }).then(() => {
        }).catch(() => {
        })
      } else {
        this.isshow = true
      }
    },
    contact() {
      this.phoneisShow = true
    },
    feedback() {
      this.$router.push('/index/out/feedback')
    },
    about() {
      this.$router.push('/index/out/about')
    },
    AccountSetting(url) {
      this.$router.push({path: '/index/out/Account', name: 'AccountSetting', params: { photo: url}})
    // console.log(url)
    },
    invite() {
      if (!!localStorage.getItem('username')) {
        this.showFriend = true
      }
    },
    beforeClose(action, done) {
      if (action === 'confirm') {
        if (!this.username || !this.tel) {
          Toast('请输入姓名或手机号')
          done(false)
          return
        }
        if (!(/^[1][3,4,5,6,7,8][0-9]{9}$/.test(this.tel))) {
          Toast('手机号格式错误')
          done(false)
          return
        }
        // setTimeout(done, 1000)
      } else if (action === 'cancel') {
        this.username = ''
        this.tel = ''
        done() // 关闭
      }
    }
  }
}
</script>

<style scoped lang="less">
  @import "~styles/index.less";
  @import "~styles/variable.less";
  *{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .out-box{
    width: 100%;
    .newversion{
      position: fixed;
      top: 5rem;
      left: 2rem;
      .close{
        position: absolute;
        top: 0.2rem;
        right:0rem;
        color: #fff;
      }
      img{
        width: 6rem;
        /*height: 4rem;*/
      }
      .checkbtn{
        width: 4rem;
        height: 0.8rem;
        line-height: 0.8rem;
        border-radius: 0.2rem;
        position: absolute;
        bottom: 1rem;
        left: 1rem;
        background-color:#ff7414;
        border: none;
      }
    }
   .out-one{
     margin-bottom: 0.2rem;
     li{
       width: 100%;
       height: 1.6rem;
       border-bottom: 1px solid #ccc;
       display: flex;
       justify-content: space-between;
       align-items: center;
       background-color: #fff;
       padding: 0.2rem 0.5rem;
       .fs(30);
       img{
         width: 0.8rem;
         height: 0.8rem;
         vertical-align: middle;
         margin-left: 5.4rem;
         margin-right: 0.5rem;
       }
     }
     li:last-child{
       border-bottom: none;
       span:nth-child(2){
         margin-left: 5.4rem;
       }
     }
   }
.dialog-inp{
  height:1.2rem!important;
  line-height: 1.2rem;
  .van-field__label{
   text-indent: 0.5rem;
  }

    }
  }

</style>



